import { RendererCanvas, getImgFromURL, COLOR_BLENDING, hex2RGBA } from '../../../lib'
import { LocalTintCanvas } from './LocalTintCanvas';
export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(divHost: any) {
        this.divHost = divHost;

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '1000px';
        div.style.height = '600px';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new Renderer(div);

        // console.log(this.renderer);
        // !! you should execute it for rendering
        // this.start();
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

export class Renderer extends RendererCanvas {
    img: HTMLImageElement | undefined = undefined
    rotation = 0.0;
    m: number[] = [0, 0];

    switch = true;
    capture: LocalTintCanvas | undefined = undefined;

    tints: LocalTintCanvas[] = [];
    constructor(div: any) {
        super(div);

        this.canvas.style.position = 'relative';

        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation
        // 

        // getImgFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/img/person/HaminHaram.jpg').then( (img: HTMLImageElement) => {
        getImgFromURL('https://www.glenstone.org/wp-content/uploads/2018/07/AV_Landscape-Hero-Contour-2993-1276x800.jpg').then((img: HTMLImageElement) => {
            // console.log(img)
            this.img = img;

        });
        const divInput = document.createElement('input');
        divInput.value = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/person/HaminHaram.jpg';
        divInput.addEventListener('keydown', (e: KeyboardEvent) => {
            console.log(e);
            if (e.code === 'Enter') {
                getImgFromURL(divInput.value).then((img: HTMLImageElement) => {
                    console.log(img)
                    this.img = img;
                    this.tints = [];
                    this.capture = undefined;
                });
            }
 
        })
        this.hostDiv.appendChild(divInput);

        const divMain_01 = document.createElement('div');
        divMain_01.style.display = 'flex';

        const divLight = document.createElement('div');
        divMain_01.appendChild(divLight);
        divLight.textContent = 'Lighten  ';
        divLight.style.paddingRight = '100px';
        // divLight.style.position = 'absolute';
        divLight.style.color = 'aqua';
        divLight.onclick = () => {
            this.tints.push(new LocalTintCanvas(this, this.hostDiv.clientWidth * 0.5, this.hostDiv.clientHeight * 0.5, COLOR_BLENDING.COLOR_DODGE, [125, 125, 125]))
        }

        const divDark = document.createElement('div');
        divMain_01.appendChild(divDark);
        divDark.style.paddingRight = '100px';
        divDark.textContent = 'Darken';
        // divDark.style.position = 'absolute';
        divDark.style.color = 'aqua';
        divDark.onclick = () => {
            this.tints.push(new LocalTintCanvas(this, this.hostDiv.clientWidth * 0.5, this.hostDiv.clientHeight * 0.5, COLOR_BLENDING.OVERLAY, [0, 0, 0]))
        }


        const divswitch = document.createElement('div');
        divMain_01.appendChild(divswitch);
        divswitch.textContent = 'Toggle';
        // divswitch.style.position = 'absolute';
        divswitch.style.color = 'aqua';
        divswitch.onclick = () => {
            this.switch = !this.switch;
        }
        this.hostDiv.appendChild(divMain_01);


        const divMain_02 = document.createElement('div');
        divMain_02.style.display = 'flex';
        this.hostDiv.appendChild(divMain_02);

        const sl0 = getSlider('R1', 20, 400);
        divMain_02.appendChild(sl0.div);
        sl0.slider.onchange = () => {
            if (this.capture) {
                this.capture.radiusX = +sl0.slider.value;
            }
        }

        const sl1 = getSlider('R2', 20, 400);
        divMain_02.appendChild(sl1.div);
        sl1.slider.onchange = () => {
            if (this.capture) {
                this.capture.radiusY = +sl1.slider.value;
            }
        }


        const sl2 = getSlider('Feather', 4, 50);
        this.hostDiv.appendChild(sl2.div);
        sl2.slider.onchange = () => {
            if (this.capture) {
                this.capture.blur = +sl2.slider.value;
            }
        }

        const sl3 = getSlider('Intensity', 1, 100);
        this.hostDiv.appendChild(sl3.div);
        sl3.slider.onchange = () => {
            if (this.capture) {
                this.capture.opacity = +sl3.slider.value * 0.01;
            }
        }

        const divInputColor = document.createElement('input');
        divInputColor.type = 'color';
        divInputColor.onchange = () => {
            console.log(divInputColor.value)
            if (this.capture) {
                const colArray = hex2RGBA(divInputColor.value)
                // this.capture.opacity = +sl3.slider.value * 0.01;
                this.capture.rgb = colArray;
                console.log(colArray)
            }
        }
        this.hostDiv.appendChild(divInputColor);

        const divInputFilterName = document.createElement('input');
        divInputFilterName.onchange = () => {
            if (this.capture) {
                this.capture.globalCompositeOperation = divInputFilterName.value as GlobalCompositeOperation;
            }
        }
        this.hostDiv.appendChild(divInputFilterName);

        this.start();
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop
        this.ctx.save();
        this.ctx.translate(this.canvas.width * 0.5, this.canvas.height * 0.5)
        this.ctx.rotate(this.rotation)
        if (this.img) {
            this.ctx.drawImage(this.img, -this.img.width * 0.5, -this.img.height * 0.5);
        }


        this.ctx.restore();


        ctx.beginPath();
        ctx.arc(this.m[0], this.m[1], 5, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill()

        if (this.switch) {
        this.tints.forEach((tint, i) => {
            tint.render(ctx);
        });
    }

        // !! can stop render after this frame.
        // this.isStatic = true;
    }
    public mouseDown(x: number, y: number): void {
        this.m = [x, y];
        if (this.capture !== undefined) {
            this.capture._isCaptured = false;
        }

        this.tints.forEach((tint, i) => {
            if (tint.isHover(x, y)) {
                this.capture = tint;
            }
        });

    }
    public mouseUp(x: number, y: number): void {
        // TODO
    }
    public mouseDrag(x: number, y: number): void {
        this.m = [x, y];

        if (this.capture) {
            this.capture.translation(x, y);
        }
    }
    public mouseClick(x: number, y: number): void {
        this.m = [x, y];
        if (this.capture !== undefined) {
            this.capture._isCaptured = false;
        }
        this.capture = undefined;

        this.tints.forEach((tint, i) => {
            if (tint.isHover(x, y)) {
                this.capture = tint;
                this.capture._isCaptured = true;
            }
        });
    }
    public mouseMove(x: number, y: number) {
        this.m = [x, y];
        this.tints.forEach((tint, i) => {
            tint.isHover(x, y);
        });
    }
    public keyDown(k: KeyboardEvent): void {
        // TODO
    }
}

export const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);

    return {
        div, slider
    }
};