import { COLOR_BLENDING } from '../../../../lib';
import { distanceTo } from 'njscore';
import { Renderer } from '../';
export class LocalTintCanvas {
    public renderer: Renderer;
    x: number;
    y: number;
    radiusX: number = 50;
    radiusY: number = 50;

    blur: number = 20;
    opacity: number = 0.85;
    _isHover: boolean = false;
    _isCaptured: boolean = false;

    mPre = [0, 0];
    rgb = [255, 255, 255];
    globalCompositeOperation: GlobalCompositeOperation = 'lighten';
    constructor(renderer: Renderer, x: number, y: number, color: COLOR_BLENDING, rgb: number[]) {
        this.renderer = renderer;
        this.x = x;
        this.y = y;
        this.globalCompositeOperation = color;
        this.rgb = rgb;
    }
    public isHover(x: number, y: number) {
        if (distanceTo(this.x, this.y, 0, x, y, 0) < (this.radiusX + this.radiusY) * 0.5 ) {
            this._isHover = true;
        } else {
            this._isHover = false;
        }
        return this._isHover;
    }
    public translation(x: number, y: number) {

        // if (distanceTo(this.x - this.radiusX, this.y, 0, x, y, 0) < 20 ) {
        //     const offset = this.mPre[0] - x;
        //     this.radiusX -= offset;
        //     this.mPre = [x, y];
        //     return;
        // }


        this.x = x;
        this.y = y;
        
    }

    public render(ctx: CanvasRenderingContext2D) {

        // https://developer.mozilla.org/en-US/docs/Web/API/CanvasRenderingContext2D/globalCompositeOperation
        // ctx.globalCompositeOperation = 'destination-out';
        ctx.globalCompositeOperation = this.globalCompositeOperation;
        ctx.filter = `blur(${this.blur}px)`;
        ctx.beginPath();
        ctx.ellipse(this.x, this.y, this.radiusX, this.radiusY, 0, 0, 2 * Math.PI);
        ctx.fillStyle = `rgba(${this.rgb[0]}, ${this.rgb[1]}, ${this.rgb[2]}, ${this.opacity})`;
        ctx.fill();

        ctx.filter = "none";
        ctx.globalCompositeOperation = 'source-over'

        if (this._isHover || this._isCaptured) {
            console.log(this._isHover, this._isCaptured);
            ctx.beginPath();
            ctx.ellipse(this.x, this.y, this.radiusX, this.radiusY, 0, 0, 2 * Math.PI);
            ctx.stroke();
        }
    }
}


